    <logo-select  @getindex='getIndex'></logo-select>
    <form id="main" v-cloak>
      <div class="search-input">
      <!-- searchString 模型与文本域创建绑定 placeholder="输入搜索内容" -->
        <input type="text" v-model="searchString"  @keyup="get($event)" @keydown.enter="search()" @keydown.down="selectDown()" @keydown.up.prevent="selectUp()"/>
        <span class="search-reset" @click="clearInput()">&times;</span>
        <button class="search-btn" @click="search()">SOSO</button>
        <div class="search-select">
          <!-- transition-group也是vue2.0中的新特性,tag='ul'表示用ul包裹v-for出来的li -->
          <transition-group name="itemfade" tag="ul" mode="out-in" v-cloak>
            <li v-for="(value,index) in myData" :class="{selectback:index==now}" class="search-select-option search-select-list" @mouseover="selectHover(index)" @click="selectClick(index)" :key="value">
              {{value}}
            </li>
          </transition-group>
        </div>
      </div>
      <ul>
        <!-- 循环输出数据 -->
        <li  v-if="searchString" v-for="article in filteredArticles">
          <a v-bind:href="article.href"><img v-bind:src="article.image" /></a>
          <p>{{article.title}}</p>
        </li>
      </ul>
    </form>
    <el-main>
    <template v-for="item  in userList.slice((currentPage-1)*pagesize,currentPage*pagesize)"  @selection-change="created">
     <div class="time">
       <el-link :href="item.href" :title="item.title" target="_blank"  :underline="false">{{item.name}}</el-link>
     </div>
    </template>
    <el-pagination  background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pagesize"
      layout="total,prev, pager, next,jumper"
      :total="userList.length">
    </el-pagination>
   </el-main>
    <el-footer>
      <span data-v-656039f0="">Copyright  ©2018-2020 &nbsp &nbsp </span>
