import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import '@/theme-et/index.css'
import ElementUI from 'element-ui'

Vue.use(Router)
Vue.use(ElementUI)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    }
  ]
})



